import selfStartTask from '@/components/self-start-task.vue';
import selfInput from '@/components/self-input.vue';
import selfButton from '@/components/self-button.vue';
import timer from '@/components/timer/index.vue';
import selfUpLoad from '@/components/self-upLoad.vue';

export default {
	data() {
		return {
			detail: {},
			targ: '',
			plainTime: '',
			samples: ['合格', '不合格'],
			samplesidx: 0
		}
	},
	components: {
		selfStartTask,
		selfInput,
		selfButton,
		timer,
		selfUpLoad
	},
	onLoad(options) {
		const { detail } = options;
		if(detail) {
			this.detail = JSON.parse(detail);
			console.log('-----', this.detail)
		}
	},
	methods: {
		samplesChange(e) {
			this.samplesidx = e.target.value
		},
		plainTimeChange(e) {
			this.plainTime = e
		},
		changeHandle(imgs) {
			console.log('imgs', imgs)
		},
		okClick() {
			alert('a')
		}
	}
}
